@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: 'PT Serif', serif;
  /* background-image: url("/Images/background1.jpeg"); */
  background-color: #1B4F72;
}

.container {
  max-width: 1060px;
  margin: 0 auto;
  padding: 20px;
  background-color: #EBF5FB; }

h1 {
  font-size: 3em; }

.aboutMe {
  display: inline-block;
  width: 37%; }

.profileInfo {
  display: inline-block;
  vertical-align: top;
  width: 60%;
  padding-left: 1em;
  line-height: 1.5em; }

.headshot {
  border-radius: 180px; }

.resume,
.linkedIn,
.portfolio {
  display: inline-block;
  margin-top: 40px;
  margin-left: 20px;
  width: 50%;
  padding: 10px;
  width: 100px;
  height: 100px;
  background-color: #E3EAF3;
  position: relative;
  color: black;
  border-radius: 180px;
 }

h4 {
   margin-top: 30px;
   margin-bottom: 30px;
}

  .skills span {
  display: inline-block;
   text-decoration: none;
   color: initial;
   font-family: monospace;
   padding: 10px;
   border-radius: 12px;
   background-color: white;
   margin-right: 10px;
   margin-bottom: 30px;
 }

 .resume p,
 .linkedIn p,
 .portfolio p {
  margin-top: 25px !important;
}

.resume a,
.linkedIn a,
.portfolio a {
  text-decoration: none;
  vertical-align: middle; }

.content {
  display: inline-block;
  vertical-align: top;
  width: 60%;
  padding-left: 1em;
  line-height: 1.5em; }

.heading {
  font-size: 2em;
  text-align: center; }

.image {
  display: inline-block;
  width: 37%;
  border-radius: 10px;
  margin-right: 80px; }

.headshot {
  border-radius: 180px; }

.skilldiv {
  text-align: center; }

li.contact {
  color: #333030;
  margin-top: 20px;
  text-decoration-style: solid;
}

.profileHeading {
  display: inline-block;
  width: 37%;
}

button {
border-radius: 10px;
padding: 5px 5px;
background-color: #C6D7EC;
}

.projectDescription {
  font-size: 1.2em;
}

.ProjectHeading {
  text-align: center;
}

p .title {
  size: 1.2em;
}
.searchbox {
  size: 100px;
}
